/*** Collapse ***/
.ant-collapse {
  font-family: inherit;
  border-radius: 5px;
}

/*** Tabs ***/
.ant-tabs {
  font-family: inherit;
  color: #242424;
}

.ant-tabs-top > .ant-tabs-nav {
  margin: 0 10px;
}

.ant-tabs > .ant-tabs-nav .ant-tabs-nav-list {
  padding: 10px 0;
}

.ant-tabs-content {
  border-top: 1px solid #d3d3d3;
}

.ant-tabs-card > .ant-tabs-nav .ant-tabs-tab {
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 45px;
  background: #fff;
  border: 0;
}

.ant-tabs-card.ant-tabs-top > .ant-tabs-nav .ant-tabs-tab {
  padding: 10px 15px;
  border-radius: 5px;
  box-shadow: 0 0 8px 2px rgba(202, 202, 202, 0.2);
  transition: 0.3s ease;
}

.ant-tabs-card.ant-tabs-top > .ant-tabs-nav .ant-tabs-tab span {
  transition: all 0.3s;
}

.ant-tabs-card.ant-tabs-top > .ant-tabs-nav .ant-tabs-tab:hover {
  background-color: #ff9000;
}

.ant-tabs-card.ant-tabs-top > .ant-tabs-nav .ant-tabs-tab:hover span {
  color: #242424 !important;
}

.ant-tabs-card.ant-tabs-top > .ant-tabs-nav .ant-tabs-tab + .ant-tabs-tab {
  margin-left: 10px;
}

.ant-tabs-card.ant-tabs-top > .ant-tabs-nav .ant-tabs-tab-active {
  background: #242424;
}

.ant-tabs-card.ant-tabs-top > .ant-tabs-nav .ant-tabs-tab-active span {
  color: #ff9000;
}

/*** Popover ***/
.todos-action .ant-popover-inner {
  padding: 6px 0;
  border-radius: 5px;
}

/*** Select ***/
.ant-select-dropdown {
  border-radius: 5px;
}

.ant-select-dropdown .ant-select-item-option .ant-select-item-option-content span {
  transition: color 0.3s ease;
}

.ant-select-dropdown .ant-select-item-option-active:not(.ant-select-item-option-disabled) {
  color: #242424;
  background-color: #ff9000;
  border-radius: 5px;
}

.ant-select-dropdown
  .ant-select-item-option-active:not(.ant-select-item-option-disabled)
  .ant-select-item-option-content
  span {
  color: #242424;
}

.ant-select-dropdown
  .ant-select-item-option-selected:not(.ant-select-item-option-disabled):not(.ant-select-item-option-active)
  .ant-select-item-option-state {
  color: #ff9000;
}

.ant-select-dropdown
  .ant-select-item-option-selected:not(.ant-select-item-option-disabled)
  .ant-select-item-option-state {
  color: #242424;
  transition: 0.3s ease;
}

/*** Spinner ***/
.ant-spin {
  color: #ff9000;
}
